*{
    margin: 0;
    padding: 0;
}
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #111;
}
.nav-bar{
    position: fixed;
    height: 50px;
    
    display:flex;
    background-color:#111;
    top: 0%;
    width: 100%;
    top: 0%;
    right: 0%;
    
}
.logo{
    position: fixed;
    left: 50px;
    width: 80px;
    top: 15px;
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    top: 10px;
}
.navbar-list{
    
    list-style: none;
    padding-left: 5cm;
    padding-top: 15px;
    
}
.navbar-list li {
    display: inline;
    
}
.navbar-collapse{
display: table;

    }
.nav-link{
    text-decoration: none;
    color:whitesmoke;  
    margin:10px;
}

.banner{
    top: 5px;
    background-image: url('https://pbs.twimg.com/media/ES6jwLpUMAIqhK0?format=jpg&name=large');
    background-size: cover;
    height: 488.3px;
    color: white;
}
.content{
    padding-top: 200px;
    height: 170px;
    padding-left: 20px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .8rem;
}
.button{
    color:white;
    border: none;
    outline: none;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: .5rem;
    padding-bottom: .5rem;
    background-color: rgb(51,51,51,.5);
    border-radius: 5px;
    font-weight: 700;
    cursor: pointer;
    margin-right: .5cm;
}
button:hover{
    color: rgb(51,51,51,.5);
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1.2rem;
    height: 80px;
    max-width: 500px;
}
.fade-bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}
.row{
    margin-left: 20px;
    color: #fff;
}
.posters{
    display: flex;
    margin: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    max-height: 250px;
    margin-right: .3cm;
    margin-left: .2cm;
    cursor: pointer;
}
.small-poster{
    max-height: 150px;
    margin-right: .3cm;
    margin-left: .2cm;
    cursor: pointer;

}
.poster:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.small-poster:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
